/**
 * @type module
 * @name data-table
 */

 .data-table.module-c {
	&.attr-scroll {
		overflow-y: auto;	
		@include overflow-scroll($theme: dark);

		>table {
			min-height: 240rem;
		}
	}

	border-top: 2px solid color(normal, 020);
	border-bottom: 1px solid color(base, 010);

	>table {
		table-layout: fixed;
		width: 100%;
		height: 100%;
		@include font((outline: body, size: small-2x)...);
		border-spacing: 0;
		border-collapse: collapse;
		border-right: hidden;
		border-left: hidden;
		
		caption {
			overflow: hidden;
			margin: -1px;
			padding: 0;
			width: 1px;
			height: 1px;
			line-height: 1px;
			font-size: 1px;
			color: transparent;
			border: 0;
			clip: rect(0 0 0 0);
			clip: rect(0,0,0,0);
		}

		// laptop
		@include respond-above(large-1x) {
			@include font((outline: body, size: small-2x)...);
		}

		>thead {
			>tr {
				>th {
					padding: 10rem 24rem 10rem 0;
					color: color(normal, 010);
					@include font((weight: medium)...);
					// border-right: 1px solid color(base, 010);
					border-bottom: 1px solid color(base, 010);
					// border-left: 1px solid color(base, 010);
					background-color: color(base, 005);
				}
			}
			// laptop
			@include respond-above(large-1x) {
				>tr {
					>th {
						padding: 16rem 0 16rem 0;
					}
				}
			}
		}
		>tbody {
			>tr {
				>td {
					padding: 10rem 0;
					color: color(normal, 030);
					// border-right: 1px solid color(base, 010);
					// border-left: 1px solid color(base, 010);
					word-break: break-all;

					>.wbr {
						display: block;
					}

					a {
						color: inherit;
						
						&:not(:hover) {
							text-decoration: none;
						}
					}
					
				}
				+tr {
					>td {
						border-top: 1px solid color(base, 010);
					}
				}
			}
			// laptop
			@include respond-above(large-1x) {
				>tr {
					>td {
						padding: 10rem 0;
					}
				}
			}
		}
		>tfoot {
			>tr {
				>td {
					padding: 8rem 16rem;
					color: color(accent, 020);
					@include font((weight:bold)...);
					border-top: 1px solid color(base, 040);
					border-right: 1px solid color(base, 010);
					border-left: 1px solid color(base, 010);
					background-color: color(accent, 020, 0.1);
				}
			}
			// laptop
			@include respond-above(large-1x) {
				>tr {
					>td {
						padding: 14rem 16rem;
					}
				}
			}
		}
	}

	&.type-a {
		>table {
			text-align: left;
			
			>tbody,
			>thead {
				>tr {
					>th {
						padding-right: 24rem;
					}
					>td {
						padding-right: 24rem;
					}
				}
			}
		}
	}
}
