//  [의미 및 기능]
//  * 절대적으로 있어야 하는 구성요소
 
//  1. legend 반드시 포함
//  2. 항목이 분류 되어야 함
//  3. 항목은 label 존재 해야함
//  3. form은 단독적인 기능을 폼함할 수 있음 (form 항목과 버튼이 동일하게 존재할 수 있음)
//  4. form 가이드 영역
//  5. valid, invalid 영역
//  6. 전송 버튼
 
//  [시각적 분류]
 
//  1. legend 비노출 여부
//  2. label 비노출 여부
//  3. label 배치 (세로, 가로)

/*
 * @name submit-form
 */

 // module-a
.submit-form.module-a {
	margin: 0;
	padding: 0;
	border: hidden;
	display: flex;
	flex-direction: column;
	row-gap: 16rem;

	legend {
		@include hidden;
	}

	.form-list {
		display: flex;
		flex-direction: column;
		row-gap: 16rem;

		.form-item {
			display: flex;
			flex-direction: column;

			.form-wrap {
				display: flex;

				.form-head {
					.form-name {
						display: block;
						@include font((outline: body, size: small-1x)...);
						color: color(normal, 010);
					}
				}
				.form-body {
					display: flex;
					flex: 1 1 100%;

					.form-area {
						display: flex;
						flex: 1 1 100%;

						.form:not(.flex) {
							flex-shrink: 0;
						}
						.form.flex {
							flex: 1 1 100%;
						}
					}
				}
			}
		}
	}

	&.type-c {
		.form-wrap {
			flex-direction: column;
			row-gap: 8rem;
		}
	}

	&.style-b {
		.form-list {
			padding-top: 24rem;
			border-top: 2rem solid color(normal, 020);
		}
	}
}