/*
 * btn system reset
*/
input.btn,
button.btn {
  overflow: visible;
  margin: 0;
  padding: 0;
  cursor: pointer;
  border: hidden;
  background-color: transparent;
  appearance: none;
}

.btn svg {
  display: block;
  position: relative;
  top: 0;
  left: 0;
}

