/*
* @type module
* @name posts
*/
ul.posts-list {
	margin: 0;
	padding: 0;
	list-style: none;
}

// posts-display-module-a
.posts-display.module-a {
	.posts-list {
		.posts-item {
			.posts-wrap {
				display: flex;
				flex-direction: column;
				row-gap: 16rem;

				// laptop
				@include respond-above(large-1x) {
					row-gap: 32rem;
				}

				.posts-figure {
					.posts-thumbnail {
						display: flex;
						flex: 1 1 100%;

						.image {
							display: flex;
							flex: 1 1 100%;
							border-radius: 20rem;
						}
					}

				}
				.posts-inform {
					.posts-head {
						.posts-subject {
							margin: 0;
							@include font((outline: body, size: small-1x, weight: semi-bold)...);
							color: color(normal, 010);

							// laptop
							@include respond-above(large-1x) {
								@include font((outline: body, size: small)...);
							}
						}
					}
					.posts-data {
						margin-top: 8rem;

						.data-list {
							.data-item {
								display: flex;
								align-items: center;

								.head {
									@include hidden;
								}
								.body {
									@include font((outline: body, size: small-2x, weight: extra-light)...);
									color: color(normal, 010);
								}
							}
						}
					}
				}
			}
		}
	}
}

// posts-read-module-a
.posts-read.module-a {
	// posts-util 
	.posts-util {
		margin-bottom: 32rem;

		// laptop
		@include respond-above(large-1x) {
			margin-bottom: 40rem;
		}
		
		.btn {
			position: relative;
			display: block;
			width: 24rem;
			height: 24rem;

			// laptop
			@include respond-above(large-1x) {
				width: 40rem;
				height: 40rem;
			}

			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: color(normal, 000);
			}
			&.prev {
				&::before {
					background-image: icon(arrow-left, color(normal, 010));
				}
			}
			.text {
				@include hidden;
			}
		}
	}

	.posts-wrap {
		.posts-inform {
			display: flex;
			position: relative;
			flex-direction: column;

			// laptop
			@include respond-above(large-1x) {
				flex-direction: row;
				flex-wrap: wrap;
				justify-content: space-between;
				align-items: flex-end;
			}

			.posts-type {
				.posts-title {
					display: flex;
					align-items: baseline;
					column-gap: 4rem;
					position: relative;
					margin: 0;
					@include font((outline: head, size: small, weight: semi-bold)...);
					color: color(normal, 100);

					&::after {
						content: '';
						display: block;
						width: 4rem;
						height: 4rem;
						border-radius: 50%;
						background-color: color(accent, 010);

						// laptop
						@include respond-above(large-1x) {
							width: 6rem;
							height: 6rem;
						}
					}
					// laptop
					@include respond-above(large-1x) {
						@include font((outline: head, size: large-3x)...);
					}
				}
				// laptop
				@include respond-above(large-1x) {
					flex: 1 1 100%;
				}
			}
			.posts-head {
				.posts-subject {
					margin: 4rem 0 0 0;
					@include font((outline: body, size: small)...);
					color: color(normal, 010);

					// laptop
					@include respond-above(large-1x) {
						margin: 16rem 0 0 0;
						@include font((outline: head, size: large)...);
					}
				}
				// laptop
				@include respond-above(large-1x) {
					width: calc(100% - 100rem);
				}
			}
		
			.posts-data {
				margin-top: 16rem;

				// laptop
				@include respond-above(large-1x) {
					margin: 0 0 0 16rem;
					
				}
				.data-list {
					.data-item {
						.head {
							@include hidden;
						}
						.body {
							@include font((outline: body, size: small-2x, weight: extra-light)...);
							color: color(normal, 010);
						}
					}
				}
			}
		}
		.posts-detail {
			margin-top: 32rem;
			padding: 64rem 0 48rem 0;
			border-top: 2rem solid color(normal, 040);

			// laptop
			@include respond-above(large-1x) {
				flex: 1 1 100%;
				margin-top: 80rem;
				padding: 80rem 0;
			}

		}
	}

	// post-direction
	.posts-direction {
		.data-list {
			padding: 16rem 0;
			border-top: 1rem solid color(base, 020);
			border-bottom: 1rem solid color(base, 020);
			
			.data-item {
				+.data-item {
					margin-top: 16rem;
					padding-top: 16rem;
					border-top: 1rem solid color(base, 020);
				}
				.data-wrap {
					display: flex;
					column-gap: 20rem;
					padding: 0 20rem;

					.data-head {
						display: flex;
						align-items: center;
						flex-shrink: 0;
						column-gap: 4rem;

						&::after {
							display: block;
							content: "";
							width: 24rem;
							height: 24rem;
						}

						.data-name {
							@include font((outline: body, size: small-1x)...);
							color: color(accent, 010);
						}
					}
					.data-body {
						display: flex;
						flex: 1 1 100%;
						align-items: center;
						justify-content: space-between;
						@include font((outline: body, size: small-1x,)...);
						
						a {
							@include ellipsis(1);
							color: color(normal, 020);

							&:not(:hover) {
								text-decoration: none;
							}
						}
					}
				}

				&.next {
					.data-wrap {
						.data-head {
							&::after {
								background-image: icon(small-chevron-down, color(accent, 010));
							}
							.data-name {
								color: color(accent, 010);
							}
						}
					}
				}

				&.prev {
					.data-wrap {
						.data-head {
							&::after {
								background-image: icon(small-chevron-up, color(accent, 010));
							}
							.data-name {
								color: color(accent, 010);
							}
						}
					}
				}
			}
		}
	}

	// button-display
	>.button-display {
		margin: 48rem 0;
		.button-area {
			.btn {
				max-width: 104rem;
			}
		}

		// laptop
		@include respond-above(large-1x) {
			margin: 80rem 0;

			.button-area {
				.btn {
					max-width: 136rem;
				}
			}
		}
	}

}

// posts-read-module-b
.posts-read.module-b {
	.posts-wrap {
		.posts-inform {
			display: flex;
			flex-direction: column;
			position: relative;

			&::after {
				order: 3;
				content: '';
				display: block;
				position: relative;
				left: 0;
				width: calc(100vw - 20rem);
				height: 192rem;
				margin: 56rem 0 32rem 0;
				background-image: var(--background-image);
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				border-top-left-radius: 260rem;
				border-bottom-left-radius: 260rem;

				//tablet
				@include respond-above(large) {
					position: absolute;
					bottom: 0;
					left: 50%;
					width: 50vw;
					height: 232rem;
					margin: 0;
				}

				//laptop
				@include respond-above(large-1x) {
					height: 520rem;
				}
			}

			//tablet
			@include respond-above(large) {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
			}

			.posts-head {
				display: flex;
				flex-direction: column;
				row-gap: 4rem;
				order: 1;

				.posts-subject {
					display: flex;
					align-items: baseline;
					column-gap: 4rem;
					margin: 0;

					@include font((outline: head, size: small, weight: semi-bold)...);
					color: color(normal, 100);

					// laptop
					@include respond-above(large-1x) {
						@include font((outline: head, size: large-3x)...);
					}

					&::after {
						content: '';
						display: block;
						width: 4rem;
						height: 4rem;
						border-radius: 50%;
						background-color: color(accent, 010);
		
						// laptop
						@include respond-above(large-1x) {
							width: 6rem;
							height: 6rem;
						}
					}

				}
				.posts-summary {
					margin: 0;

					@include font((outline: body, size: small)...);

					// laptop
					@include respond-above(large-1x) {
						margin: 16rem 0 0 0;
						@include font((outline: head, size: large)...);
					}
				}

			}
			.posts-data {
				order: 2;
				margin-top: 20rem;

				.data-list {
					display: flex;
					flex-direction: column;
					row-gap: 4rem;

					//laptop
					@include respond-above(large) {
						flex-direction: row;
					}

					.data-item {
						display: flex;
						column-gap: 8rem;
						position: relative;

						//laptop
						@include respond-above(large) {
							+.data-item {
								padding-left: 12rem;
								margin-left: 12rem;

								&::before {
									content: '';
									display: block;
									position: absolute;
									top: 50%;
									left: 0;
									transform: translateY(-50%);
									width: 1rem;
									height: 10rem;
									background-color: color(normal, 040);
								}


							}
						}

						.head {
							min-width: 56rem;
							@include font((outline: body, size: small-2x, weight: light)...);
							color: color(normal, 020);

							//laptop
							@include respond-above(large) {
								min-width: auto;
							}
						}
						.body {
							@include font((outline: body, size: small-2x)...);
							color: color(normal, 020);
	
							.text {
								color: color(normal, 020);
							}
						}
					}
				}
			}
			.posts-body {
				order: 4;

				//tablet
				@include respond-above(large) {
					width: 50%;
					padding-right: 24rem;
					margin-top: 80rem;
					box-sizing: border-box;
				}

				//laptop
				@include respond-above(large-1x) {
					padding-right: 160rem;
					margin-top: 264rem;
				}

				.portfolio-description {
					//laptop
					@include respond-above(large) {
						display: flex;
						flex-direction: column;
						justify-content: flex-end;
						min-height: 240rem;
					}

					.portfolio-subject {
						margin: 0;
						letter-spacing: 2rem;
						@include font((outline: body, size: small-1x, weight: bold, family: openSans)...);
						color: color(accent, 030);
						margin-bottom: 16rem;
					}
					.portfolio-summary {
						@include font((outline: body, size: small-1x, weight: extra-light)...);
					}
				}
			}
		}

		.posts-detail {
			margin: 64rem -20rem 0 -20rem;

			img {
				vertical-align: top;
			}

			//laptop
			@include respond-above(large-1x) {
				position: relative;
				left: calc(50% - 50vw);
    			width: 100vw;
				margin: 140rem 0 0 0;
				text-align: center;
			}
		}
	}
}

// paginaition
.pagination.module-a.style-a {
	display: flex;
	align-items: center;
	column-gap: 24rem;
	position: fixed;
	z-index: 3;
	bottom: 56rem;
	left: 50%;
	transform: translateX(-50%);
	padding: 0 24rem;
	background-color: color(normal, 010);
	border: 1rem solid color(base, 080);
	border-radius: 32rem;
	box-sizing: border-box;

	&.type-b {
		justify-content: flex-end;
	}

	&.type-c {
		justify-content: center;
	}

	.pagination-util .btn {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		width: 20rem;
		height: 20rem;
		@include font((outline: body, size: small-4x)...);
		color: color(base, 070);
		box-sizing: border-box;
	}

	.pagination-util {
		display: flex;
		
		.button-area {
			display: flex;
			
			+.button-area {
				// margin-left: 4rem;
			}

			.btn {
				text-decoration: none;
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;

				.btn-text {
					position: relative;
					z-index: -1;
				}
				
				&.prev {background-image: icon(arrow-left, color(normal, 000), color(normal, 010));}
				&.next {background-image: icon(arrow-right, color(normal, 000), color(normal, 010));}
				&.list {background-image: icon(menu, color(normal, 000), color(normal, 010));}
			}
		}
	}

	.pagination-data {
		display: flex;
		justify-content: center;
		align-items: center;
		column-gap: 8rem;
		height: 48rem;
		@include font((outline: body, size: small-2x)...);
		color: color(normal, 000);
		text-align: center;

		.pagination-sign {
			margin: 0 0.25em;
		}

		.pagination-current {
			@include font((weight: bold)...);
			color: color(normal, 000);
		}
	}
}