.bui-tab-target {
	&:not(.active) {
		@include hidden;
	}
}


/* tab-display */
.tab-display {
	.tab-list {
		display: flex;
		margin: 0;
		padding: 0;

		.tab-item {
			box-sizing: border-box;
			list-style: none;
			
			.tab-text {
				display: flex;
				position: relative;
				justify-content: center;
				align-items: center;
				text-decoration: none;
				text-align: center;
			}

			&.current {
				z-index: 1;
			}
		}
	}

	// tab type
	&.type-a { 
		.tab-list{
			justify-content: flex-start;
		}
	}

	&.type-b { 
		.tab-list{
			justify-content: flex-end;
		}
	}

	&.type-c { 
		.tab-list{
			justify-content: center;
		}
	}

	&.type-d { 
		.tab-list{
			.tab-item {
				flex: 1 1 100%;
				width: 100%;
			}
		}
	}
}

/*
 * module-c outline
 *
 * @ $line-height
 * @ $font-size
 * @ $font-weight
 * @ $margin-left = $line-height/2 + 2
 */
 $tab-module-c-outline: (
	'medium'		40rem 20rem 1.5 700 0,
);

.tab-display.module-c {
	.tab-list {
		.tab-item {
			display: flex;
			align-items: center;

			.tab-text {
				position: relative;
				color: color(base, 020);
			}
			&.current {
				.tab-text {
					color: color(base, 080);
				}
			}

		}
	}

	&.style-a {
		.tab-list {
			position: relative;
			
			&::before {
				content: ' ';
				display: block;
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translate(-50%, 0);
				width: 100%;
				background-color: color(base, 020);
			}

			.tab-item {
				display: flex;

				.tab-text {
					flex: 1 1 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					text-align: center;
				}

				&.current {
					.tab-text {
						@include font((weight: bold)...);
						&::before {
							content: ' ';
							display: block;
							position: absolute;
							right: 0;
							bottom: 0;
							left: 0;
							background-color: color(base, 080);
						}
					}
				}
			}
		}
	}

	// outline
	@each $name, $height, $line-height, $font-size, $font-weight, $margin in $tab-module-c-outline {
		&.#{$name} {
			.tab-list {
				&::before {
					height: 1rem;
				}

				.tab-item {
					line-height: $line-height;
					font-size: calc($line-height / $font-size);
	
					.tab-text {
						height: $height;
						padding: 0 calc($height * 0.25);
	
						&::before {
							height: calc(1rem * 2);
							border-radius: 1rem;
						}
					}
					&.current {
						.tab-text {
							font-weight: $font-weight;
						}
					}
				}
			}
		}
	}
}


.dropdown.module-a {
	position: relative;
	.dropdown-wrap {
		.dropdown-head {
			.dropdown-subject {
				margin: 0;
				cursor: pointer;

				&::before {
					content: attr(data-bui-tab-selected);
					display: flex;
					align-items: center;
					justify-content: space-between;
					position: relative;
					padding: 0 12rem 0 16rem;
					height: 48rem;
					@include font((outline: body, size: small-1x, weight: semi-bold)...);
					color: color(normal, 010);
					box-sizing: border-box;
					background-repeat: no-repeat;
					background-position: calc(100% - 13rem) 50%;
					background-size: 24rem;
					background-image: icon(chevron-down, color(normal, 020));
					
				}
				
				.dropdown-name {
					@include hidden;
				}
			}
		}

		.dropdown-body {
			display: none;
			position: absolute;
			z-index: 1;
			top: 100%;
			left: 0;
			right: 0;
			
			.navi-list {
				margin-top: 8rem;
				overflow-y: auto;
				@include overflow-scroll(default, dark);
				border-radius: 8rem;
				border: 1rem solid color(base, 020);
				background-color: color(base, 000);
				box-shadow: 0 4rem 8rem 0 rgb(0 0 0 / 15%), 0 1rem 2rem 0 rgb(0 0 0 / 10%);

				.navi-item {
					display: flex;
					
					.navi-text {
						flex: 1 1 100%;
						display: flex;
						z-index: 2;
						padding: 12rem 16rem;
						@include font((outline: body, size: small-1x, weight: extra-light)...);
						color: color(normal, 020);
						text-decoration: none;
						outline-offset: -1rem;
					}

					&:focus,
					&:hover {
						background-color: color(base, 005);
					}

					&.current {
						.navi-text {
							background-color: color(base, 005);
							@include font((outline: body, size: small-1x, weight: regular)...);
						color: color(normal, 010);
						}
					}
				}
			}
		}
	}
	&:focus-within {
		.dropdown-wrap {
			.dropdown-body {
				display: block;
			}
		}
	}
	&.style-a {
		.dropdown-wrap {
			.dropdown-head {
				.dropdown-subject {
					// width: 98rem;
					&::before {
						border-radius: 24rem;
						border: 1rem solid color(normal, 020);						
					}
				}
			}
	
			.dropdown-body {
				.navi-list {
					// width: 192rem;
					// height: calc(48rem * 7);
				}
			}
		}
	}

	&.style-b {
		.dropdown-wrap {
			.dropdown-head {
				.dropdown-subject {
					width: 312rem;
	
					&::before {
						border-radius: 8rem;
						border: 1rem solid color(base, 020);
					}
				}
			}
	
			.dropdown-body {
				.navi-list {
					width: 312rem;
					height: calc(48rem * 5);
				}
			}
		}
	}
}