.data-display.module-a[class*=type-] {
	.data-list {
		display: flex;
		// flex-direction: column;

		.data-item {
			row-gap: 16rem;

			// laptop
			@include respond-above(large-1x) {
				row-gap: 32rem;
			}

			.data-wrap {
				display: flex;
				position: relative;
				row-gap: 8rem;

				// laptop
				@include respond-above(large-1x) {
					row-gap: 16rem;
				}

				.data-head {
					display: block;
					@include font((outline: body, size: small-1x, weight: semi-bold)...);
					color: color(normal, 100);

					// laptop
					@include respond-above(large-1x) {
						@include font((outline: head, size: small)...);
					}

					.data-name {
						display: block;

					}
				}
				.data-body {
					@include font((outline: body, size: small-2x)...);
					color: color(normal, 020);

					// laptop
					@include respond-above(large-1x) {
						@include font((outline: body, size: small-1x)...);
					}

					.wbr {
						display: block;
					}

					.em {
						color: color(normal, 010);
						@include font((weight: semi-bold)...);
					}
				}
			}
		}
	}

	&.style-b {
		.data-list {
			.data-item {
				display: flex;
				flex-direction: column;
				align-items: center;
				background-color: color(normal, 000);
				border-radius: 20rem;
				
				// mobile
				@include respond-below(large-1x) {
					// width: 152rem;
					padding: 20rem 0 24rem 0;
					row-gap: 16rem;
				}
				
				// laptop
				@include respond-above(large-1x) {
					// width: 296rem;
					padding: 48rem 0;
					row-gap: 32rem;
				}

				&::before {
					content: '';
					display: block;
					width: 56rem;
					height: 56rem;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					
					// laptop
					@include respond-above(large-1x) {
						width: 96rem;
						height: 96rem;
					}
				}
				.data-wrap {
					align-items: center;

					.data-head {
						@include font((family: openSans)...);
					}

					.data-body {
						text-align: center;
					}
				}
			}
		}
	}

	&.style-c {
		.data-list {
			.data-item {
				display: flex;
				flex-direction: column;
				align-items: center;
				background-color: color(normal, 000);
				border-radius: 20rem;

				// &:hover {
				// 	.data-wrap {
				// 		.data-head {
				// 			color: color(accent, 010);
				// 			transition: color 0.25s;
				// 		}
				// 	}
				// }
				
				// mobile
				@include respond-below(large-1x) {
					// width: 152rem;
					padding: 20rem 0 24rem 0;
					row-gap: 16rem;
				}
				
				// laptop
				@include respond-above(large-1x) {
					// width: 296rem;
					padding: 88rem 0;
					row-gap: 32rem;
				}

				&::before {
					content: '';
					display: block;
					width: 56rem;
					height: 56rem;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					
					// laptop
					@include respond-above(large-1x) {
						width: 80rem;
						height: 80rem;
					}
				}
				.data-wrap {
					align-items: center;

					.data-body {
						text-align: center;
					}
				}
			}
		}
	}



	&.type-a {
		.data-list {
			.data-wrap {

				.data-head {
					flex-shrink: 0;
				}

				.data-body {
					flex: 1 1 100%;
				}
			}
		}
	}

	&.type-b {
		.data-list {
			.data-wrap {
				justify-content: space-between;

				.data-head {
					flex-shrink: 0;
				}

				.data-body {
					flex: 1 1 100%;
					text-align: right;
				}
			}
		}
	}

	&.type-c {
		.data-list {
			.data-wrap {
				flex-direction: column;
			}
		}
	}
	
	&.type-d {
		.data-list {
			.data-item {
				.data-wrap {
					@include respond-below(large) {
						flex-direction: column;
					}
	
					.data-head {
						flex-shrink: 0;
					}
	
					.data-body {
						flex: 1 1 100%;
					}
				}
			}
		}
	}
}

.data-display.module-b{
	.data-list {
		display: flex;

		.data-item {
			.data-wrap {
				display: flex;
				flex-direction: column;
				position: relative;

				.data-head {
					display: block;
					// @include font((outline: body, size: small-1x)...);
					// color: color(normal, 100);

					.data-subject {
						margin: 0;

						.data-name {
							display: block;
						}
					}
				}
				.data-body {
					@include font((outline: body, size: small-2x)...);
					color: color(normal, 020);

					.wbr {
						display: block;
					}

					.em {
						color: color(normal, 010);
					}
				}
			}
		}
	}

	&.style-b {
		.data-list {
			.data-item {
				display: flex;
				column-gap: 20rem;
				
				// laptop
				@include respond-above(large-1x) {
					column-gap: 48rem;
				}

				&::before {
					content: '';
					display: block;
					flex-shrink: 0;
					width: 56rem;
					height: 56rem;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
					// background-color: rgba(255,0,0,0.1);
					
					// laptop
					@include respond-above(large-1x) {
						width: 80rem;
						height: 80rem;
					}
				}

				.data-wrap {
					row-gap: 4rem;

					// laptop
					@include respond-above(large-1x) {
						row-gap: 8rem;
					}

					.data-head {
						.data-subject {
							@include font((outline: body, size: small-2x, weight: semi-bold, family: openSans)...);
							color: color(normal, 010);
							letter-spacing: 2rem;
						}
					}
					.data-body {
						@include font((outline: body, size: small-1x)...);
						color: color(normal, 020);
	
					}
				}
			}
		}
	}

	&.style-c {
		.data-list {
			counter-reset: order-count;
			.data-item {
				display: flex;
				column-gap: 24rem;
				counter-increment: order-count;

				// laptop
				@include respond-above(large-1x) {
					align-items: center;
					column-gap: 32rem;
				}

				&::before {
					content: counter(order-count, decimal-leading-zero);
					display: block;
					display: flex;
					flex-shrink: 0;
					align-items: center;
					justify-content: center;
					width: 56rem;
					height: 56rem;
					border: 1rem solid color(primary, 050);
					border-radius: 50%;
					@include font((outline: body, size: small-2x)...);
					color: color(accent, 010);
					
					// laptop
					@include respond-above(large-1x) {
						width: 72rem;
						height: 72rem;
					}
				}

				.data-wrap {
					row-gap: 8rem;

					// laptop
					@include respond-above(large-1x) {
						row-gap: 10rem;
					}

					.data-head {
						.data-subject {
							@include font((outline: head, size: small, weight: semi-bold)...);
							color: color(normal, 010);
						}
					}
					.data-body {
						@include font((outline: body, size: small-1x)...);
						color: color(normal, 010);
					}
				}
			}
		}
	}
}


