.data-display.module-c {
	&:not(.swiper) {

	}
	
	>.data-list {
		>.data-item {
			display: flex;	
			flex-direction: column;
			align-items: center;
			
			.data-wrap {
				display: flex;
				flex-direction: column;
				align-items: stretch;
				position: relative;
				width: 100%;

				&::before {
					content: '';
					display: block;
					aspect-ratio: 1 / 1;
					background-image: var(--background-image);
					border-radius: 50%;
					background-repeat: no-repeat;
					background-position: center;
					background-size: cover;
				}

				.data-head {
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;

					>.data-caption {
						display: flex;
						margin: 0;

						.wbr {
							display: block;
						}

						.data-item {
							position: relative;
						}
					}

					.data-subject {
						margin: 0;

						.data-name {
							display: block;

							&:not(:hover) {
								text-decoration: none;
							}
							
							&::before {
								content: '';
								display: block;
								position: absolute;
								top: 0;
								right: 0;
								bottom: 0;
								left: 0;
							}
							
							.wbr {
								display: block;
							}
						}
					}
				}
			}
		}
	}

	// style-a
	&.style-a {
		>.data-list {
			>.data-item {
				.data-wrap {
					row-gap: 16rem;
					max-width: 302rem;

					.data-head {
						row-gap: 4rem;

						// laptop
						@include respond-above(large-1x) {
							position: absolute;
							top: 0;
							right: 0;
							bottom: 0;
							left: 0;
							padding: 0 16rem;
							border-radius: 50%;
							background-color: color(accent, 010, 0);
							transition: background-color 0.25s ease;

							.data-caption,
							.data-subject {
								opacity: 0;
								transform: translateY(8rem);
								transition: opacity 0.5s ease, transform 0.5s ease;
							}

							&:hover {
								background-color: color(accent, 010, 0.9);

								.data-caption,
								.data-subject {
									opacity: 1;
									transform: translateY(0);
								}
							}
						}

						>.data-caption {
							@include font((outline: body, size: small-2x, family: openSans)...);
							color: color(normal, 030);

							// laptop
							@include respond-above(large-1x) {
								color: color(normal, 000);
							}

							.data-item {
								+.data-item {
									margin-left: 8rem;
									padding-left: 8rem;
	
									&::before {
										content: '';
										display: block;
										position: absolute;
										top: 50%;
										left: 0;
										transform: translateY(-50%);
										width: 1rem;
										height: 8rem;
										background-color: color(normal, 030);
	
										// laptop
										@include respond-above(large-1x) {
											background-color: color(normal, 000);
										}
									}
								}
							}
						}

						.data-subject {
							@include font((outline: body, size: small-1x, weight: semi-bold, family: openSans)...);
						
							// laptop
							@include respond-above(large-1x) {
								@include font((outline: head, size: medium)...);
							}

							.data-name {
								color: color(normal, 010);
								text-align: center;

								// laptop
								@include respond-above(large-1x) {
									color: color(normal, 000);
								}
							}
						}
					}
				}
			}
		}
	}

	// style-b
	&.style-b {
		.data-list {
			.data-item {
				.data-wrap {
					max-width: 302rem;

					.data-head {
						row-gap: 12rem;
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						padding: 0 16rem;
						border-radius: 50%;
						background-color: color(normal, 010, 0.8);
						transition: background-color 0.25s ease;

						.data-caption {
							@include font((outline: body, size: small-2x, weight: semi-bold, family: openSans)...);
							color: color(normal, 000);
							border-bottom: 1rem solid color(normal, 000);
						}

						.data-subject {
							@include font((outline: body, size: small-1x)...);
							
							.data-name {
								color: color(normal, 000);
								text-align: center;

								&::after {
									content: '';
									display: block;
									width: 24rem;
									height: 24rem;
									margin: 8rem auto 0 auto;
									background-repeat: no-repeat;
									background-position: center;
									background-size: cover;
									background-image: icon(arrow-up-right, color(normal, 000));
								}
							}
						}
					}

					// laptop
					@include respond-above(large-1x) {
						.data-head {
							row-gap: 24rem;
							background-color: color(normal, 010, 0);

							.data-caption,
							.data-subject {
								opacity: 0;
								transform: translateY(8rem);
								transition: opacity 0.5s ease, transform 0.5s ease;
							}

							.data-subject {
								@include font((outline: head, size: medium)...);

								.data-name {
									&::after {
										width: 32rem;
										height: 32rem;
										margin: 8rem auto 0 auto;
									}
								}
							}

							&:hover {
								background-color: color(normal, 010, 0.9);

								.data-caption,
								.data-subject {
									opacity: 1;
									transform: translateY(0);
								}
							}
						}
					}
				}
			}
		}
	}
}

// 채용 절차
.order-display.recruitment-process {
	>.order-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		counter-reset: order-count;
		column-gap: 20rem;
		row-gap: 32rem;

		// tablet
		@include respond-above(large) {
			flex-wrap: nowrap;
		}

		>.order-item {
			width: calc(50% - (64rem / 2));
			position: relative;
			counter-increment: order-count;

			&:nth-child(1) {
				&::before {
					display: none;
				}
				.order-wrap {
					.data-head {
						background-color: color(normal, 040);
					}
				}
			}
			&:nth-child(2) {
				.order-wrap {
					.data-head {
						background-color: color(normal, 030);
					}
				}
			}
			&:nth-child(3) {
				.order-wrap {
					.data-head {
						background-color: color(normal, 020);
					}
				}
			}
			&:nth-child(4) {
				.order-wrap {
					.data-head {
						background-color: color(accent, 010);
					}
				}
			}

			// mobile
			@include respond-below(large) {
				&:nth-child(3) {
					&::before {
						display: none;
					}
				}
			}

			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 52rem;
				left: -44rem;
				width: 24rem;
				height: 24rem;
				background-image: icon(small-chevron-right, color(base, 020));

				// tablet
				@include respond-above(large) {
					left: -24rem;
				}

				// laptop
				@include respond-above(large-1x) {
					top: 92rem;
					left: -40rem;
					width: 28rem;
					height: 28rem;
				}
			}

			// tablet
			@include respond-above(large) {
				width: calc(50% - (64rem / 2));
			}
		
			// laptop
			@include respond-above(large-1x) {
				width: calc(25% - (140rem / 4));
			}

			.order-wrap {
				display: flex;
				flex-direction: column;
				row-gap: 16rem;
				align-items: center;
				justify-content: center;

				.data-head {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 128rem;
					height: 128rem;
					position: relative;
					background-color: color(base, 030);
					border-radius: 50%;

					// laptop
					@include respond-above(large-1x) {
						width: 215rem;
						height: 215rem;
					}

					.data-name {
						display: flex;
						flex-direction: column;
						row-gap: 4rem;

						@include font((outline: head, size: small-1x, weight: bold)...);
						color: color(normal, 000);
						text-align: center;
						
						&::before {
							content: counter(order-count, decimal-leading-zero);
							display: block;
							@include font((outline: body, size: small-4x, weight: bold)...);
							color: color(normal, 000);

							// laptop
							@include respond-above(large-1x) {
								@include font((outline: body, size: small-1x)...);
							}
						}

						// laptop
						@include respond-above(large-1x) {
							@include font((outline: head, size: medium)...);
						}
					}
					
				}
				.data-body {
					@include font((outline: body, size: small-1x)...);
					color: color(normal, 020);
					text-align: center;

					// laptop
					@include respond-above(large-1x) {
						@include font((outline: body, size: small)...);
					}

					.wbr {
						display: block;
					}

					.em {
						color: color(normal, 010);
					}
				}
			}
		}
	}
	>.data-list.module-a {
		margin-top: 32rem;
		padding-top: 8rem;
		border-top: 1rem solid color(base, 020);

		// laptop
		@include respond-above(large-1x) {
			margin-top: 64rem;
		}
	}
}

/*
* @ data-display.service
*
*/
.data-display.service {
	filter: drop-shadow(2rem 4rem 30rem rgba(48, 51, 77, 0.1));

	>.data-list {
		display: flex;
		column-gap: 20rem;

		@include respond-above(large-1x) {
			column-gap: 32rem;
		}

		>.data-item {
			display: flex;
			flex-direction: column;
			position: relative;
			border-radius: 20rem;
			overflow: hidden;
			background-color: #fff;
			
			// mobile
			@include respond-below(large-1x) {
				min-width: 272rem;
			}

			&::before {
				content: "";
				display: block;
				width: 100%;
				height: 204rem;
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;

				@include respond-above(large-1x) {
					height: 322rem;
				}
			}

			&::after {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				height: 204rem;
				background-color: rgba(0,0,0,.5);
				background-image: icon(circle-arrow-right, color(normal, 000));
				background-repeat: no-repeat;
				background-position: center;
				background-size: 48rem 48rem;
			}
			
			// laptop
			@include respond-above(large-1x) {
				width: calc(33.3333% - 16rem);

				&::after {
					height: 322rem;
					opacity: 0;
					transition: opacity 0.25s ease;
				}

				&:hover {
					&::after {
						opacity: 1;
					}
				}
			}

			>.data-wrap {
				display: flex;
				flex-direction: column;
				align-items: center;
				row-gap: 8rem;
				padding: 20rem 0;
				
				>.data-head {
					>.data-name {
						display: block;
						@include font((outline: head, size: small-1x, weight: semi-bold, family: openSans)...);
						color: color(normal, 010);

						@include respond-above(large-1x) {
							@include font((outline: head, size: small)...);
						}
					}
				}
				>.data-body {
					@include font((outline: body, size: small-1x)...);
					color: color(normal, 010);

					.wbr {
						display: block;
						text-align: center;

					}
				}

				>.data-util {
					.data-more {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						z-index: 1;

						.more-text {
							@include hidden;
						}
					}
				}
			}
		}
	}
}



