.data-display.partners {
	.data-list {
		display: flex;
		flex-wrap: wrap;
		gap: 16rem;
		
		// laptop
		@include respond-above(large-1x) {
			column-gap: 32rem;
			row-gap: 48rem;
		}

		.data-item {	
			display: flex;
			position: relative;
			width: calc((100% / 2) - 8rem);
			height: 72rem;
			border-radius: 20rem;
			align-items: center;
			justify-content: center;
			background-color: color(normal, 000);

			// mobile
			@include respond-below(large-1x) {		
				padding: 16rem 20rem;
				width: calc((100% / 2) - 48rem);
			}

			.data-wrap {
				display: block;
				align-items: center;
				justify-content: center;

				.data-head {
					display: flex;
					align-items: center;
					justify-content: center;

					img {
						display: block;
						filter: grayscale(1);
						opacity: 0.5;
					}

				}
				.data-body {
					// mobile
					@include respond-below(large-1x) {		
						display: none;
					}
				}
			}


			// laptop
			@include respond-above(large-1x) {
				width: calc((100% / 5) - 26rem);
				height: 112rem;

				.data-wrap {
					&::before {
						content: '';
						display: block;
						position: absolute;
						bottom: calc(100% + 21rem);
						left: calc(50% - 8rem);
						width: 16rem;
						height: 10rem;
						visibility: hidden;
						opacity: 0;
						fill: color(accent, 020);
						background-color: color(accent, 020);
						transform: translate(0, 8rem);
						clip-path: path('M6.97635 8.04783L0.538086 0H16.5381L10.0998 8.04783C9.29917 9.04864 7.777 9.04864 6.97635 8.04783Z');
						transition: transform 0.25s ease-in-out, opacity 0.25s ease-in-out, visibility 0.25s ease-in-out;
					}

					.data-head {
						img {
							filter: grayscale(1);
							opacity: 0.5;
						}
					}

					.data-body {
						position: absolute;
						bottom: calc(100% + 30rem);
						left: 50%;
						transform: translate(-50%, 8rem);
						@include font((outline: body, size: small-1x, weight: extra-light)...);
						color: color(normal, 000);
						background-color: color(accent, 020);
						padding: 20rem 34rem;
						border-radius: 20rem;
						max-width: 454rem;
						text-align: center;
						visibility: hidden;
						opacity: 0;
						transition: transform 0.25s ease-in-out, opacity 0.25s ease-in-out, visibility 0.25s ease-in-out;

						.para {
							margin: 0;
							white-space: nowrap;
						}
					}
				}

				&:hover {
					box-shadow: (0px 4px 20px rgba(99, 100, 102, 0.1));

					.data-wrap {
						&::before {
							visibility: visible;
							opacity: 1;
							transform: translate(0, 0);
						}

						.data-head {
							img {
								filter: grayscale(0);
								opacity: 1;
							}
						}

						.data-body {
							visibility: visible;
							opacity: 1;
							transform: translate(-50%, 0);
						}
					}
				}

				&:nth-child(5n - 4) {
					.data-wrap {
						.data-body {
							left: 0;
							transform: translate(0, 8rem);
						}
					}

					&:hover {
						.data-wrap {
							.data-body {
								transform: translate(0, 0);
							}
						}
					}
				}

				&:nth-child(5n) {
					.data-wrap {
						.data-body {
							left: auto;
							right: 0;
							transform: translate(0, 8rem);
						}
					}

					&:hover {
						.data-wrap {
							.data-body {
								transform: translate(0, 0);
							}
						}
					}
				}
			}
		}
	}
}