
/*
* @ mark module accent
*
* @ $text-color
* @ $border-color
* @ $background-color
*/
$board-module-accent: (
	'normal-00': (
		'none' color(normal, 100) transparent transparent,
		'line' color(normal, 100) color(normal, 100) color(normal, 000),
		'fill' color(normal, 000) color(normal, 100) color(normal, 100),
	),
	'normal-01': (
		'none' color(normal, 030) transparent transparent,
		'line' color(normal, 030) color(base, 020) color(base, 000),
		'fill' color(normal, 030) color(base, 020) color(base, 020),
	),
	'normal-02': (
		'none' color(normal, 030) transparent transparent,
		'line' color(normal, 030) color(normal, 030) color(normal, 000),
		'fill' color(normal, 000) color(normal, 030) color(normal, 030),
	),
	'normal-03': (
		'none' color(base, 050) transparent transparent,
		'line' color(normal, 040) color(normal, 040) color(normal, 000),
		'fill' color(base, 060) color(base, 010) color(base, 010),
	),
	'accent-01': (
		'none' color(accent, 010) transparent transparent,
		'line' color(accent, 010) color(accent, 010) color(accent, 000),
		'fill' color(accent, 000) color(accent, 010) color(accent, 010),
	),
);

/*
 * module-a outline
 *
 * @ $padding
 * @ $line-height
 * @ $font-size
 * @ $font-weight
 * @ $border-width
 * @ $border-radius
 */  
 $board-module-a-outline: (
	'small-1x'		12rem 12rem 18rem 1.50 500 1rem 8rem,
	'small'			10rem 12rem 20rem 1.50 500 1rem 8rem,
	'medium'		14rem 12rem 24rem 1.50 500 1rem 8rem,
	'large'			16rem 12rem 28rem 1.50 500 1rem 8rem,
	'large-1x'		18rem 12rem 32rem 1.50 500 1rem 8rem,
);
/**
 * @type module
 * @name info-board
 * @modle a
 */
 .info-board.module-a {
	 .board-wrap {
		display: flex;
		flex-direction: column;

		.board-head {
			display: flex;
			flex-direction: column;
			row-gap: 8rem;

			.board-subject {
				margin: 0;
				.board-name{
					display: block;
					color: color(normal, 030);
					@include font((outline: body, size: small-1x, weight: medium)...);
				}	
			}
		}

		.board-util {
			.button-area {
				display: flex;
				column-gap: 6rem;
			}
		}
	}
	&[class*="attr-"] {
		.board-wrap {
			&::before {
				content: '';
				display: block;
				width: 64rem;
				height: 64rem;
				background-repeat: no-repeat;
				background-position: center;
				background-size: 100%;
			}
		}
	}

	&.attr-done {
		.board-wrap::before {
			background-image: url(./../images/svg/none_file.svg);
		}
	}

	// style
	&.style-d {
		background-color: color(base, 010);
		border-top: 2rem solid color(normal, 020);
		border-bottom: 1rem solid color(base, 020);
		
	}

	// type
	&.type-a {
	}

	&.type-b {
		.board-wrap {
			.board-head {
				align-items: flex-end;
				text-align: right;
			}
			align-items: flex-end;
		}
	}

	&.type-c {
		.board-wrap {
			.board-head {
				text-align: center;
				align-items: center;
			}
			align-items: center;
		}
	}

	//style
	&[class*=style-b],
	&[class*=style-c] {
		border-style: solid;
	}

	// accent
	@each $accent-name in $board-module-accent {
		&.#{nth($accent-name, 1)} {
			@each $type-name, $color, $border-color, $background-color in map-get($board-module-accent, nth($accent-name, 1)) {
				&.type-#{$type-name} {
					background-color: $background-color;
					border-color: $border-color;		
					.board-wrap {
						.board-head {
							.board-name {
								color: $color;
							}
							.board-summary {
								color: $color;
							}
						}
					}

					&[class*=attr-caution] .board-subject::before {background-image: icon(circle-caution, $color);}
					&[class*=attr-information] .board-subject::before {background-image: icon(circle-caution, $color);}
				}
			}
		}
	}


	// outline
	@each $name, $padding, $row-gap, $line-height, $font-size, $font-weight, $border-width, $border-radius in $board-module-a-outline {
		&.#{$name} {
			line-height: $line-height;
			font-size: calc($line-height / $font-size);

			&[class*=style-] {
				padding: $padding;
				border-width: $border-width;
			}

			&.style-b {
				border-radius: $padding * 0.4;
			}
	
			&.style-c {
				border-radius: $line-height * 0.5;
			}

			&[class*=attr-] .board-subject::before {
				margin-right: $line-height * 0.1;
				width: $line-height;
				height: $line-height;
			}

			.board-wrap {
				.board-head {
					.board-subject {
						font-weight: $font-weight;
						font-size: $line-height * 0.8;
						line-height: $line-height;
					}

					~.board-body {
						margin-top: $line-height * 0.8;
					}
				}
			}
		}
	}	
}


/**
 * @type module
 * @name noti-board
 * @modle a
 */
.noti-board {
	.board-wrap {
		.board-head {
			display: flex;	

			.board-summary {
				display: flex;
				margin: 0;
				column-gap: 4rem;

				.board-name {
					display: block;
				}
			}
		}
	}

	&.type-b {
		display: flex;
		justify-content: right;

		.board-wrap {
			.board-head {
				align-items: flex-end;
				text-align: right;
			}
		}
	}
	
	&.type-c { 
		display: flex;
		justify-content: center;

		.board-wrap {
			display: flex;
			flex-wrap: wrap;
			flex-flow: column;
			justify-content: center;
			align-items: center;
		}
	}

	&[class*=ltr-] .board-summary::before,
	&[class*=rtl-] .board-summary::after {
		content: '';
		flex-shrink: 0;
		display: block;
		mask-repeat: no-repeat;
		mask-size: cover;
	}

	&[class*=ltr-info] .board-summary::before,
	&[class*=rtl-info] .board-summary::after {
		mask-image: icon(info, color(normal, 000));
	}

	// accent
	@each $name, $font-color in $text-accent {
		&.#{$name} {
			color: $font-color;
			
			&[class*=ltr-info] .board-summary::before,
			&[class*=rtl-info] .board-summary::after {
				background-color: $font-color;
			}
		}
	}

	// outline
	@each $name, $line-height, $font-size in map-get($font-outline, 'body') {
		&.#{$name} {
			.board-wrap {
				.board-head {
					.board-summary {
						line-height: map-get($font-outline, 'body', $name, line-height);
						font-size: map-get($font-outline, 'body', $name, font-size);
					}
				}
			}

			&[class*=ltr-] .board-summary::before,
			&[class*=rtl-] .board-summary::after {
				width: map-get($font-outline, 'body', $name, line-height);
				height: map-get($font-outline, 'body', $name, line-height);
			}

			&.style-a {
				padding: map-get($font-outline, 'body', $name, line-height) * 0.5;
				border: 1rem solid color(base, 010);
			}

			&.style-b {
				padding: map-get($font-outline, 'body', $name, line-height) * 0.5;
				background-color: color(base, 010);
				border: 1rem solid color(base, 020);
				border-radius: 4rem;
			}
		}
	}
}



.info-board.module-b {
	.board-list {
		display: flex;
		flex-direction: column;
		row-gap: 24rem;

		@include respond-above(large-1x) {
			flex-direction: row;
			column-gap: 32rem;
		}

		.board-item {
			@include respond-above(large-1x) {
				width: calc(50% - 16rem);
			}
	
			.board-wrap {
				display: flex;
				flex-direction: column;
				align-items: center;
				row-gap: 24rem;

				@include respond-above(large-1x) {
					row-gap: 32rem;
				}

				.board-head {
					display: block;
					img {
						display: block;
						height: 56rem;
						
						@include respond-above(large-1x) {
							height: 80rem;
						}
					}
				}

				.board-util {
					.button-display.module-a {
						.button-area {
							.btn {
								transition: background-color 0.25s;
								.btn-text {
									transition: color 0.25s;
									@include font((weight: semi-bold)...);

									&::after {
										transition: background-image 0.25s;
									}
								}
							}
						}
					}
				}
			}
		}
	}

	&.style-b {
		.board-list {
			.board-item {
				padding: 60rem 8rem;
				border-radius: 20rem;
				background-color: color(base, 005);
		
				@include respond-above(large-1x) {
					padding: 145rem 16rem;
				}

				.board-wrap {
					.board-util {
						&:hover {
							.button-display.module-a {
								.button-area {
									.btn {
										background-color: color(normal, 010);
										.btn-text {
											color: color(normal, 000);

											&::after {
												// background-image: icon(small-arrow-right, color(normal, 000));
												background-color: color(normal, 010);
											}
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}

.info-board.module-c {
	.board-wrap {
		display: flex;
		flex-direction: column;
		row-gap: 20rem;
		.board-head {
			display: flex;
			flex-direction: column;
			row-gap: 24rem;
			.board-caption {
				margin: 0;
				svg {
					display: block;
					width: 148rem;
					height: 20rem;
					fill : color(normal, 020);

					@include respond-above(large-1x) {
						width: 178rem;
						height: 24rem;
					}
				}
			}
			.board-subject {
				margin: 0;
				@include font((outline: head, size: medium, weight: extra-light)...);
				color: color(normal, 040);

				@include respond-above(large-1x) {
					@include font((outline: head, size: large-2x)...);
				}

				.board-name {
					display: block;

					.wbr {
						display: block;
					}
				}
			}
		}
		.board-util {
			.button-display.module-a {
				.button-area {
					@include respond-above(large-1x) {
						column-gap: 8rem;
					}

					.btn {
						background-color: transparent;
						transition: background-color 0.25s, box-shadow 0.25s, border-color 0.25s;
						@include respond-above(large-1x) {
							.btn-text {
								transition: color 0.25s;

								&::after {
									transition: background-image 0.25s;
								}
							}
							&:hover {
								background-color: color(normal, 000);
								box-shadow: 2px 4px 18px rgba(0, 0, 0, 0.1);
								border-color: color(normal, 000);

								&.symbol-rtl-small-arrow-right {
									.btn-text {
										color: color(normal, 010);

										&::after {
											// background-image: icon(small-arrow-right, color(normal, 010));
											background-color: color(normal, 010);
										}
									}
								}

								&.symbol-rtl-download {
									.btn-text {
										color: color(normal, 010);

										&::after {
											background-image: icon(download, color(normal, 010));
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}
}