/*
 * btn system reset
*/

input.btn,
button.btn {
	overflow: visible;
	margin: 0;
	padding: 0;
	cursor: pointer;
	border: hidden;
	background-color: transparent;
	appearance: none;
}

.btn svg {
	display: block;
	position: relative;
	top: 0;
	left: 0;
}

/*
 * btn module-b system
 */

/*
 * @ module-b accent
 *
 * @ $text-color
 * @ $border-color
 * @ $background-color
*/
$btn-module-b-accent: (
	'normal-00': (
		'none' color(normal, 100) transparent transparent,
		'line' color(normal, 100) color(normal, 100) color(normal, 000),
		'fill' color(normal, 000) color(normal, 100) color(normal, 100),
	),
	'normal-01': (
		'none' color(normal, 010) transparent transparent,
		'line' color(normal, 010) color(normal, 010) color(normal, 000),
		'fill' color(normal, 000) color(normal, 010) color(normal, 010),
	),
	'normal-02': (
		'none' color(normal, 020) transparent transparent,
		'line' color(normal, 020) color(normal, 020) color(normal, 000),
		'fill' color(normal, 000) color(normal, 020) color(normal, 020),
	),
	'normal-03': (
		'none' color(normal, 020) transparent transparent,
		'line' color(normal, 020) color(normal, 030) color(normal, 000),
		'fill' color(normal, 000) color(normal, 030) color(normal, 030),
	),
	'normal-04': (
		'none' color(normal, 040) transparent transparent,
		'line' color(normal, 040) color(normal, 040) color(normal, 000),
		'fill' color(normal, 000) color(normal, 040) color(normal, 040),
	),
	'normal-05': (
		'none' color(normal, 020) transparent transparent,
		'line' color(normal, 000) color(normal, 000) color(base, 080),
		'fill' color(normal, 020) color(normal, 040) color(normal, 040),
	),
	'accent-01': (
		'none' color(accent, 010) transparent transparent,
		'line' color(accent, 010) color(accent, 000) color(accent, 000),
		'fill' color(accent, 000) color(accent, 010) color(accent, 010),
	),
	'accent-02': (
		'none' color(accent, 020) transparent transparent,
		'line' color(accent, 020) color(accent, 020) color(accent, 000),
		'fill' color(accent, 000) color(accent, 020) color(accent, 020),
	),
	'accent-03': (
		'none' color(accent, 030) transparent transparent,
		'line' color(accent, 030) color(accent, 030) color(accent, 000),
		'fill' color(accent, 000) color(accent, 030) color(accent, 030),
	),
);

/*
 * module-b outline
 *
 * @ $height
 * @ $line-height
 * @ $font-size
 * @ $font-weight
 * @ $border-width
 */  
$btn-module-b-outline: (
	'small-1x'		24rem 16rem 1.50 600 1rem,
	'small'			28rem 18rem 1.50 600 1rem,
	'medium'		32rem 20rem 1.50 600 1rem,
	'large'			36rem 20rem 1.50 600 1rem,
	'large-1x'		40rem 20rem 1.50 600 1rem,
	'large-2x'		48rem 20rem 1.25 600 1rem,
	'large-3x'		56rem 20rem 1.25 600 1rem,
);

// btn module-b reset
 a.btn.module-b {
	 text-decoration: none;
 }

 .btn.module-b {
	// general
	display: inline-flex;
	align-items: center;
	justify-content: center;
	@include font((family: default)...);
	box-sizing: border-box;

	&:not(.type-none) {
		border-style: solid;
	}

	&.style-b {
		border-radius: 4rem;
	}

	&.disabled {
		color: color(base, 020);
		border-color: color(base, 010);
		background-color: color(base, 010);
		pointer-events: none;
	}

	.btn-text {
		display: flex;
		align-items: center;
		justify-content: center;
		white-space: nowrap;
		text-align: center;
	}

	&.transform-size {
		@include respond-above(large) {
			transition: all 0.25s ease;

			.wbr {
				display: block;
				
				&:nth-child(1) {
					position: absolute;
					opacity: 0;
					overflow: hidden;
					max-width: 0;
					transition: max-width 0.25s ease;
				}
			}
	
			&:hover {
				.btn-text {
					.wbr {
						&:nth-child(1) {
							position: relative;
							opacity: 1;
							max-width: 150rem;
						}
					}
				}
			}
		}
	}

	// icon
	&[class*=ltr-] .btn-text::before,
	&[class*=rtl-] .btn-text::after {
		content: '';
		display: block;
		mask-repeat: no-repeat;
		mask-position: center;
		mask-size: cover;
	}

	// small-arrow-right
	&[class*=ltr-small-arrow-right] .btn-text::before,
	&[class*=rtl-small-arrow-right] .btn-text::after {
		mask-image: icon(small-arrow-right, color(base, 100));
	}

	// small-arrow-left
	&[class*=ltr-arrow-left] .btn-text::before,
	&[class*=rtl-arrow-left] .btn-text::after {
		mask-image: icon(arrow-left, color(base, 100));
	}

	// download
	&[class*=ltr-download] .btn-text::before,
	&[class*=rtl-download] .btn-text::after {
		mask-image: icon(download, color(base, 100));
	}

	// chevron-down
	&[class*=ltr-chevron-down] .btn-text::before,
	&[class*=rtl-chevron-down] .btn-text::after {
		mask-image: icon(chevron-down, color(base, 100));
	}

	// accent
	@each $accent-name in $btn-module-b-accent {
		&.#{nth($accent-name, 1)} {

			// @debug "divider offset: #{nth($accent-name, 1)}";
			@each $type-name, $color, $border-color, $background-color in map-get($btn-module-b-accent, nth($accent-name, 1)) {

				&.type-#{$type-name}:not(.disabled) {
					--color: #{$color};
					--border-color: #{$border-color};
					--background-color: #{$background-color};

					color: var(--color);
					
					&:not(.type-none) {
						border-color: var(--border-color);
						background-color: var(--background-color);
					}

					// icon
					&[class*=ltr-] .btn-text::before,
					&[class*=rtl-] .btn-text::after {
						background-color: var(--color);
					}


					&.transform-color {
						transition: color 0.25s ease, border-color 0.25s ease, background-color 0.25s ease;

						.btn-text::before,
						.btn-text::after {
							transition: background-color 0.25s ease;
						}

						&:hover {
							--color: #{$background-color};
							--background-color: #{$color};
						}
					}
				}
			}
		}
	}

	// outline
	@each $name, $height, $line-height, $font-size, $font-weight, $border-width in $btn-module-b-outline {
		&.#{$name} {
			padding-right: $height * 0.5;
			padding-left: $height * 0.5;
			height: $height;
			font-weight: $font-weight;
			line-height: $line-height;
			font-size: calc($line-height / $font-size);

			.btn-text {
				column-gap: $line-height * 0.2;
			}

			&:not(.type-none) {
				border-width: $border-width;
			}

			&.style-c {
				border-radius: $height * 0.5;
			}

			// icon
			&[class*=ltr-] .btn-text::before,
			&[class*=rtl-] .btn-text::after {
				width: $line-height;
				min-width: $line-height;
				height: $line-height;
			}
			
			&[class*=ltr-] {
				padding-left: $height * 0.35;
			}

			&[class*=rtl-] {
				padding-right: $height * 0.35;
			}

			&.transform-size {
				@include respond-above(large) {
					padding: 0;

					&[class*=ltr-]:hover {
						padding-right: $height * 0.5;
						padding-left: $height * 0.35;
					}

					&[class*=rtl-]:hover {
						padding-right: $height * 0.35;
						padding-left: $height * 0.5;
					}
				}
			}

			&.transform-size:not(.type-none) {
				min-width: $height;
			}
		}
	}
}

// button-display
.button-display.module-a {
	display: flex;

	.button-area {
		display: flex;

		.btn.large-2x {
			&~*,
			&+* {
				margin-left: 8rem;
			}
		}
	}

	&.type-a {
		.button-area {
			flex: 1 1 100%;
			justify-content: flex-start;
		}
	}

	&.type-b {
		.button-area {
			flex: 1 1 100%;
			justify-content: flex-end;
		}
	}

	&.type-c {
		.button-area {
			flex: 1 1 100%;
			justify-content: center;
		}
	}

	&.type-d {
		justify-content: space-between;
	}

	&.type-e {
		.button-area {
			.btn {
				// mobile
				@include respond-below(large-1x) {
					flex-grow: 1 1 100%;
				}
				
				// laptop
				@include respond-above(large-1x) {
					flex-shrink: 0;
				}
			}
		}
	}
}