/* goods-display */
.goods-display {
	.goods-list {
		margin: 0;
		padding: 0;
		list-style: none;
	}
	.goods-head {
		.goods-order-number {
			margin: 0;
		}
		.goods-subject {
			margin: 0;
		}
	}
}


.goods-display.module-a {
	.goods-list {		
		.goods-item {
			.goods-wrap {
				display: flex;
				align-items: flex-start;
				column-gap: 16rem;
				width: 100%;

				.goods-figure {
					flex-shrink: 0;
					display: block;
					width: 136rem;
					height: 106rem;

					// laptop
					@include respond-above(large-1x) {
						width: 220rem;
						height: 172rem;
					}

					.goods-thumbnail {
						display: block;

						img {
							display: block;
							width: 100%;
						}
					}
				}

				.goods-inform {
					flex: 1 1 100%;
					display: flex;
					flex-direction: column;
					row-gap: 8rem;

					// laptop
					@include respond-above(large-1x) {
						row-gap: 16rem;
					}

					.goods-head {
						display: flex;
						flex-direction: column;
						row-gap: 4rem;
						
						.goods-subject {
							margin: 0;
							@include font((outline: body, size: small, weight: bold)...);
							color: color(normal, 100);

							// laptop
							@include respond-above(large-1x) {
								@include font((outline: body, size: medium)...);
							}

							.goods-name {
								display: block;
							}
						}

						.goods-summary {
							margin: 0;
							@include font((outline: body, size: small-1x)...);
							color: color(normal, 100);
						}
					}

					.data-body {

					}
				}
			}
		}
	}

	&.style-a {
		.goods-list {
			position: relative;
	
			// laptop
			@include respond-above(large-1x) {
				display: flex;
				flex-wrap: wrap;
			}
			
			.goods-item {
				display: flex;
				column-gap: 16rem;
	
				&::before {
					position: absolute;
					right: 0;
					left: 0;
					height: 1rem;
					background-color: color(base, 010);
				}
	
				// mobile
				@include respond-below(large-1x) {
					+.goods-item {
						&::before {
							content: '';
						}
	
						margin-top: 16rem;
		
						.goods-wrap {
							padding-top: 16rem;
						}
					}
				}
				
				// laptop
				@include respond-above(large-1x) {
					display: flex;
					width: 50%;
					column-gap: 24rem;
					
					&:nth-child(n+3) {
						&::before {
							content: '';
						}
	
						margin-top: 32rem;
	
						.goods-wrap {
							padding-top: 32rem;
						}
					}
				}
			}
		}
	}
}